.nav {
  padding: 50px;
}
.list {
  li {
    font-size: 24px;
    line-height: 120%;
    &:not(:first-child) {
      margin-top: 25px;
    }
  }
}


.modal {
  background: rgba(#000, .4);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  &-btn {
    width: 266px;
    font-size: 16px;
    text-transform: uppercase;
    margin-top: 25px;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 912px;
    max-width: 100%;
    position: relative;
    background: #292A29;
    box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.3);
    padding: 40px 40px 60px;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto 0;
      max-width: calc(100% - 30px);
    }
  }
  &__close{
    position: absolute;
    top: 20px;
    right: 20px;
    background-color: transparent;
    padding: 0;
    svg {
      width: 20px;
      height: 20px;
    }
  }
  &__content {
    text-align: center;
  }
  &__form {
    margin-top: 85px;
    &-flex {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &-group {
      &:not(:first-child) {
        margin-left: 50px;
      }
    }
    &-btn {
      width: 266px;
      font-size: 16px;
      text-transform: uppercase;
      margin: 40px auto 0;
    }
  }
  &__check {
    position: relative;
    margin-top: 40px;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      opacity: 0;
      &:checked ~ label:after {
        background-image: url("../img/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 10px 8px;
      }
    }
    label {
      font-size: 12px;
      line-height: 120%;
      color: #9498A0;
      padding-left: 30px;
      display: inline-block;
      position: relative;
      a {
        text-decoration: underline;
        position: relative;
        z-index: 3;
      }
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 18px;
        height: 18px;
        border: 1px solid #9498A0;
      }
    }
  }
}