.header {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  padding: 10px 0;
  &2 {
    background-image: url("../img/header-bg.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: relative;
    padding: 10px 0 25px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__center {
  }
  &__nav {
    display: flex;
    align-items: center;
  }
  &__list {
    display: flex;
    align-items: center;
    li {
      &:not(:first-child) {
        margin-left: 60px;
      }
    }
  }
  &__link {
    font-size: 16px;
    line-height: 120%;
    text-transform: uppercase;
    color: #9498A0;
  }
  &__btn {
    width: 231px;
    height: 46px;
    font-size: 16px;
    line-height: 120%;
    text-transform: uppercase;
    background: transparent;
    color: #F8970A;
    border-top: 1px solid #F8970A;
    border-left-color: transparent;
    border-right-color: transparent;
    border-bottom-color: transparent;
    border-radius: 25px;
    margin-left: 100px;
    &:hover {
      background: #F8970A;
      box-shadow: 0px 0px 20px rgba(248, 151, 10, 0.8);
      color: #fff;
    }
  }
  &__item {
    position: relative;
    text-transform: unset;
    &--active {
      .header__item-hidden {
        opacity: 1;
        pointer-events: unset;
        transition: all 0.3s;
      }
    }
    &-show {
      display: inline-block;
      span {
        transition: all 0.5s;
        cursor: pointer;
      }
      &:hover {
        span {
          color: #F8970A;
        }
      }
    }
    &-hidden {
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      min-width: 230px;
      opacity: 0;
      pointer-events: none;
      padding-top: 40px;
    }
    &-link {
      font-size: 16px;
      line-height: 120%;
      text-align: center;
      color: #9498A0;
      display: block;
      &:not(:first-child) {
        margin-top: 10px;
      }
    }
  }
  &__hidden-inner {
    position: relative;
    padding: 15px;
    background: rgba(1, 0, 16, 0.94);
    border-top: 1px solid #F8970A;
    box-shadow: 0px 1px 7px rgba(173, 173, 173, 0.25);
    &:after {
      content: '';
      position: absolute;
      top: -20px;
      left: 50%;
      transform: translate(-50%, 0);
      border: 10px solid transparent;
      border-bottom: 10px solid #F8970A;
    }
  }
  &__connection {
    display: flex;
    align-items: center;
  }
  &__numbers {
    margin-left: 15px;
  }
  &__number {
    font-size: 22px;
    line-height: 120%;
    text-transform: uppercase;
    color: #9498A0;
    display: block;
    &:not(:first-child) {
      margin-top: 5px;
    }
  }
  &__tel-mob  {
    display: none;
  }
  &__burger {
    width: 45px;
    height: 25px;
    position: relative;
    z-index: 4;
    cursor: pointer;
    display: none;
    span {
      position: absolute;
      background: #F8970A;
      height: 1px;
      transition: all 0.5s;
      &:nth-child(1) {
        top: 0;
        left: 0;
        width: 100%;
      }
      &:nth-child(2) {
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 100%;
      }
      &:nth-child(3) {
        left: 0;
        bottom: 0;
        width: 100%;
      }
    }
  }
}