@media screen and (max-width: 768px) {
  .modal {
    &__dialog {
      padding: 40px 20px;
    }
    &__form {
      margin-top: 40px;
      &-flex {
        display: block;
      }
      &-group {
        &:not(:first-child) {
          margin: 25px 0 0 0;
        }
        label {
          font-size: 14px;
        }
        input {
          width: 100%;
          font-size: 14px;
          &:focus,
          &.input-active {
            &+label {
              font-size: 12px;
            }
          }
        }
      }
      &-btn {
        font-size: 14px;
      }
    }
    &__check {
      margin-top: 20px;
      label {
        font-size: 12px;
        &:after {
          top: -5px;
          transform: translate(0, 0);
        }
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .modal {
    &__form {
      &-btn {
        width: 100%;
      }
    }
  }
}