@media screen and (max-width: 1400px) {
  .contacts {
    &__items {
      max-width: 1210px;
      margin: 80px auto;
    }
    &__item {
      &:not(:first-child) {
        margin-left: 70px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .contacts {
    &__img {
      display: none;
    }
    &__items {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 50px;
      max-width: 550px;
      margin: 80px auto 0;
    }
    &__item {
      &:not(:first-child) {
        margin-left: 0;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .contacts {
    padding:0 0 45px 0;
    &__items {
      gap: 20px;
      margin-top: 30px;
      max-width: 480px;
    }
    &__item {
      width: 230px;
      min-height: 210px;
      padding: 15px;
      i {
        width: 45px;
        height: 45px;
      }
    }
    .map2 {
      height: 300px;
      margin-top: 30px;
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts {
    &__items {
     max-width: 375px;
      gap: 15px;
    }
   &__item {
     width: 180px;
     min-height: 180px;
   }
    &__subtitle {
      font-size: 14px;
    }
    &__number {
      font-size: 14px;
    }
    &__location-wrap {
      span {
        font-size: 14px;
      }
    }
    &__mail {
      font-size: 14px;
    }
    &__mail-wrap {
      span {
        font-size: 14px;
      }
    }
    &__chart-wrap {
      margin-top: 20px;
      span {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 430px) {
  .contacts {
    &__items {
      grid-template-columns: 1fr;
      max-width: 100%;
    }
    &__item {
      width: 100%;
    }
  }
}