@media screen and (max-width: 1500px) {
  .header {
    &__list {
      li {
        &:not(:first-child) {
          margin-left: 30px;
        }
      }
    }
    &__btn {
      margin-left: 50px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .header {
    &__burger {
      display: block;
      &--active {
        span {
          &:nth-child(1) {
            transform: rotate(-45deg);
            top: 50%;
          }
          &:nth-child(2) {
            display: none;
          }
          &:nth-child(3) {
            transform: rotate(45deg);
            top: 50%;
          }
        }
      }
    }
    &__center {
      position: absolute;
      z-index: 3;
      top: 0;
      left: -100%;
      width: 100%;
      height: 100vh;
      background: #010010;
      transition: all 0.5s;
      flex-direction: column;
      padding: 150px 15px 30px;
      &--active {
        left: 0;
      }
    }
    &__numbers {
      display: none;
    }
    &__tel {
      display: none;
      &-mob {
        display: block;
      }
    }
    &__logo {
      width: 125px;
      display: block;
      img {
        width: 100%;
        height: auto;
      }
    }
    &__nav {
      display: block;
      width: 100%;
    }
    &__list {
      display: block;
      li {
        &:not(:first-child) {
          margin: 15px 0 0 0;
        }
      }
    }
    &__btn {
      margin: 30px 0 0 0;
      border: 1px solid #F8970A;
    }
    &__item {
      &--active {
        .header__item-hidden {
          display: block;
        }
      }
      &-hidden {
        position: relative;
        top: 0;
        left: 0;
        transform: translate(0, 0);
        opacity: 1;
        display: none;
        padding-top: 0;
      }
      &-link {
        text-align: start;
      }
    }
    &__hidden-inner {
      padding: 15px 0 15px 15px;
      border: unset;
      box-shadow: none;
      &:after {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    background: #0C0D1D;
  }
}