.btn-orange {
  height: 60px;
  background: #F8970A;
  box-shadow: 0px 0px 20px rgba(248, 151, 10, 0.8);
  border-radius: 30px;
  color: #FFFFFF;
  border: 1px solid transparent;
  &:hover {
    box-shadow: 0px 0px 40px rgba(248, 151, 10, 0.8);
  }
}
.form-group {
  border: none;
  margin: 0;
  padding: 0;
  position: relative;
  label {
    transition: all .1s;
    pointer-events: none;
    position: absolute;
    left: 15px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 18px;
    line-height: 120%;
    color: #fff;
  }
  input {
    font-size: 18px;
    line-height: 120%;
    background: transparent;
    border: none;
    border-bottom: 1px solid #FFFFFF;
    color: #FFFFFF;
    padding: 15px;
    &::placeholder {
      color: #FFFFFF;
    }
    &:focus {
      border-bottom: 1px solid #F8970A;
    }
    &:focus,
    &.input-active {
      &+label {
        top: -10px;
        left: 0;
        font-size: 14px;
      }
    }
  }
}
.display2 {
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  position: relative;
  display: inline-block;
  padding-bottom: 28px;
  span {
    color: #F8970A;
  }
  &:after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    width: 300px;
    height: 1px;
    background: #F8970A;;
  }
}
.display2-white {
  font-size: 42px;
  line-height: 120%;
  color: #F8970A;
  position: relative;
  display: inline-block;
  padding-bottom: 28px;
  span {
    color: #FFFFFF;
  }
  &:after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    width: 300px;
    height: 1px;
    background: #F8970A;;
  }
}
.breadcrumbs {
  padding: 30px 0 15px;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__item {
    margin-bottom: 15px;
    font-size: 14px;
    line-height: 120%;
    font-weight: 500;
    position: relative;
    &:not(:last-child) {
      margin-right: 16px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -10px;
        transform: translate(0, -50%);
        width: 4px;
        height: 1px;
        background: #292A29;
      }
    }
    &--active {
      font-weight: 300;
    }
  }
}