@media screen and (max-width: 1900px) {
  .repair {
    &__item-line {
      svg {
        width: 290px;
      }
    }
    &__item-desc {
      bottom: 31px;
    }
  }
}

@media screen and (max-width: 1550px) {
  .isuzu {
    &__left {
      flex-shrink: 0;
    }
    &__form {
      &-flex {
        display: block;
      }
      &-group {
        &:not(:first-child) {
          margin: 25px 0 0 0;
        }
      }
      &-btn {
        margin: 25px 0 0 0;
      }
    }
  }
  .stages2 {
    .swiper-slide {
      padding: 60px 25px 25px;
    }
  }
  .models {
    .swiper-wrapper {
      grid-template-columns: repeat(5,calc((100% / 5) - (20px * 4 / 5)));
    }
  }
  .repair {
    &__images {
      max-width: 800px;
    }
    &__item6 {
      top: 55%;
    }
  }
}

@media screen and (max-width: 1400px) {
  .brands2 {
    &__items {
      grid-template-columns: 1fr 1fr 1fr 1fr;
      &:not(._show){
        .brands2__item{
          &:nth-child(4)~.brands2__item{
            display: none;
          }
        }
      }
    }
  }
  .description {
    &__left {
      flex-shrink: 0;
      max-width: 530px;
    }
    &__right {
      flex-shrink: unset;
    }
  }
  .stages2 {
    &__btns {
      margin-bottom: 20px;
    }
  }
  .models {
    .swiper-wrapper {
      grid-template-columns: repeat(4,calc((100% / 4) - (20px * 3 / 4)));
    }
  }
  .repair {
    &__item-line {
      svg {
        width: 250px;
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .repair-main {
    &__inner {
      padding: 80px 40px 80px 40px;
    }
  }
  .description {
    &__top {
      display: block;
    }
    &__left {
      max-width: 100%;
    }
    &__right {
      width: 100%;
      margin: 30px 0 0 0;
    }
  }
  .repair {
    &__img-bg {
      display: none;
    }
    &__nav {
      justify-content: start;
    }
    &__images {
      margin: 0 0 0 auto;
      max-width: 680px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .isuzu {
    &__left {
      flex-shrink: 1;
    }
  }
  .models {
    .swiper-wrapper {
      grid-template-columns: repeat(3,calc((100% / 3) - (20px * 2 / 3)));
    }
  }
}

@media screen and (max-width: 992px) {
  .repair-main {
    &__btns {
      display: block;
    }
    &__btn2 {
      margin: 25px 0 0 0;
    }
    &__btn3 {
      margin: 25px 0 0 0;
    }
  }
  .isuzu {
    &__inner {
      flex-direction: column;
    }
    &__right {
      margin: 30px 0 0 0;
    }
  }
  .repair {
    &__list-item {
      &--active {
        span:nth-child(2) {
          text-decoration: unset;
        }
      }
    }
    &__item {
      display: none;
    }
    &__images {
      max-width: 100%;
      margin: 30px auto 0;
    }
  }
  .brands2 {
    &__items {
      grid-template-columns: 1fr 1fr 1fr;
      &:not(._show){
        .brands2__item{
          &:nth-child(3)~.brands2__item{
            display: none;
          }
        }
      }
    }
  }
  .models {
    &__btns {
      margin-bottom: 20px;
    }
  }
}

@media screen and (max-width: 768px) {
  .repair-main {
    &__inner {
      padding: 45px 25px 45px 25px;
    }
    &__desc {
      font-size: 14px;
      margin-top: 20px;
    }
    &__btns {
      margin-top: 40px;
    }
    &__btn {
      font-size: 14px;
    }
    &__btn2 {
      height: 50px;
      font-size: 14px;
      margin-top: 20px;
    }
    &__btn3 {
      height: 50px;
      font-size: 14px;
      margin-top: 20px;
    }
  }
  .repair {
    &__list-item {
      span {
        &:nth-child(2) {
          font-size: 16px;
        }
      }
    }
  }
  .desc {
    padding: 20px 0 0;
    &__text {
      font-size: 14px;
    }
  }
  .brands2 {
    &__items {
      grid-template-columns: 1fr 1fr;
      &:not(._show){
        .brands2__item{
          &:nth-child(2)~.brands2__item{
            display: none;
          }
        }
      }
    }
    &__btn {
      font-size: 14px;
    }
  }
  .description {
    padding: 45px 0;
    &__line {
      display: none;
      &-mob {
        display: block;
      }
      &-mob2 {
        display: block;
      }
    }
    &__line2 {
      display: none;
    }
    &__top {
      margin-top: 30px;
    }
    &__bottom {
      margin-top: 30px;
      &-img {
        margin-top: 20px;
        padding-bottom: 35%;
      }
    }
    &__nav {
      margin-top: 20px;
    }
    &__list {
      &:not(:first-child) {
        margin-top: 20px;
      }
      li {
        font-size: 14px;
      }
    }
    &__desc {
      font-size: 14px;
    }
  }
  .cost2 {
    padding: 45px 0;
    &__subtitle {
      font-size: 14px;
    }
    &__form {
      &-flex {
        grid-template-columns: 1fr;
        gap: 25px;
      }
      &-group {
        label {
          font-size: 14px !important;
        }
      }
    }
  }
  .reviews {
    padding: 45px 0;
    &__slider {
      margin-top: 50px;
    }
    &-card {
      padding: 15px;
      &__img {
        width: 70px;
        height: 70px;
      }
      &__info {
        margin-left: 25px;
        strong {
          font-size: 18px;
        }
        span {
          margin-top: 10px;
        }
      }
      &__desc {
        font-size: 14px;
        margin-top: 20px;
      }
    }
    .swiper-button-prev,
    .swiper-button-next {
      width: 35px;
      height: 35px;
      svg {
        width: 10px;
      }
    }
  }
  .isuzu {
    padding: 45px 0;
    &__subtitle {
      font-size: 20px;
      margin-top: 20px;
      padding-bottom: 12px;
    }
    &__form {
      margin-top: 40px;
      padding-bottom: 40px;
      &-group {
        input {
          font-size: 14px;
        }
      }
      &-btn {
        font-size: 14px;
      }
    }
    &-info {
      grid-template-columns: 1fr;
      gap: 25px;
      &__item-desc {
        font-size: 14px;
      }
    }
  }
  .stages2 {
    padding: 0 0 45px;
    &__slider {
      margin-top: 70px;
    }
    .swiper-button-prev,
    .swiper-button-next {
      width: 35px;
      height: 35px;
      svg {
        width: 10px;
      }
    }
    &-card {
      &__btn {
        font-size: 14px;
      }
    }
  }
  .models {
    &__slider {
      margin-top: 60px;
    }
    .swiper-button-prev,
    .swiper-button-next {
      width: 35px;
      height: 35px;
      svg {
        width: 10px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .repair-main {
    &__btn {
      width: 100%;
    }
    &__btn2 {
      width: 100%;
    }
    &__btn3 {
      width: 100%;
    }
  }
  .brands2 {
    &__btn {
      width: 100%;
    }
  }
}

@media screen and (max-width: 400px) {
  .brands2 {
    &__items {
      grid-template-columns: 1fr;
      &:not(._show){
        .brands2__item{
          &:nth-child(1)~.brands2__item{
            display: none;
          }
        }
      }
    }
  }
  .stages2 {
    &-card {
      display: block;
      &__desc {
        margin: 20px 0 0 0;
      }
      &__btn {
        width: 100%;
      }
    }
  }
}