.footer {
  background: #010010;
  padding: 35px 0 80px;
  &__items {
    display: flex;
    flex-wrap: wrap;
  }
  &__item {
    max-width: 295px;
    &:not(:first-child) {
      margin-left: 150px;
    }
  }
  &__logo {
    display: block;
    margin-top: -15px;
    padding-bottom: 20px;

  }
  &__service {
    font-size: 16px;
    line-height: 130%;
    color: #9498A0;
    display: block;
    margin-top: 20px;
    span {
     text-decoration: underline;
    }
  }
  &__list {
    li {
      font-size: 16px;
      line-height: 120%;
      color: #FFFFFF;
      &:not(:first-child) {
        margin-top: 20px;
      }
    }
  }
  &__tel {
    display: flex;
    align-items: center;
  }
  &__numbers {
    margin-left: 30px;
  }
  &__number {
    font-size: 16px;
    line-height: 120%;
    display: block;
    color: #FFFFFF;
    &:not(:first-child) {
      margin-top: 8px;
    }
  }
  &__location {
    display: flex;
    margin-top: 26px;
    span {
      font-size: 16px;
      line-height: 120%;
      margin-left: 30px;
      color: #FFFFFF;
    }
  }
  &__mail {
    display: flex;
    margin-top: 26px;
    span {
      font-size: 16px;
      line-height: 120%;
      margin-left: 30px;
      color: #FFFFFF;
    }
  }
  &__chart {
    display: flex;
    margin-top: 26px;
    span {
      font-size: 16px;
      line-height: 120%;
      margin-left: 30px;
      color: #FFFFFF;
    }
  }
}