@media screen and (max-width: 1500px) {
  .services {
    .swiper-wrapper {
      grid-template-columns: repeat(6,calc((100% / 6) - (22px * 5 / 6)));
    }
  }
  .about {
    &__img-bg2 {
      display: none;
    }
  }
  .works {
    .swiper-slide {
      padding-left: 0;
    }
    &-slide {
      &__left {
        margin-right: 50px;
      }
    }
    &__btns {
      left: 30%;
    }
  }
}

@media screen and (max-width: 1400px) {
  .about {
    &__img-bg {
      display: none;
    }
    &__right {
      width: 485px;
      margin-left: 50px;
      padding-top: 0;
    }
    &__left {
      width: calc(100% - 535px);
      margin-right: 0;
    }
    &__btn {
      margin-top: 30px;
    }
  }
  .stages {
    &__btns {
      margin-bottom: 15px;
    }
  }
  .our-contacts {
    &__right {
      width: 630px;
      height: 450px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .services {
    .swiper-wrapper {
      grid-template-columns: repeat(5,calc((100% / 5) - (22px * 4 / 5)));
    }
  }
}

@media screen and (max-width: 1200px) {
  .about {
    padding: 70px 0 170px;
    &__inner {
      flex-direction: column;
    }
    &__left {
      width: 100%;
    }
    &__right {
      order: -1;
      width: 100%;
      margin: 0 0 40px 0;
    }
    &__title {
      display: flex;
      justify-content: center;
    }
    &__btn {
      position: absolute;
      left: 50%;
      bottom: -85px;
      transform: translate(-50%, 0);
      margin-top: 0;
    }
  }
  .works {
    padding: 50px 0 135px;
    &__slider {
      margin-top: 130px;
    }
    &__btns {
      top: -50px;
      left: unset;
      right: 0;
    }
    .swiper {
      overflow: unset !important;
    }
    .swiper-slide {
      display: block;
    }
    &-slide {
      &__left {
        max-width: 100%;
        margin: 0 0 30px 0;
      }
      &__right {
        width: 100%;
      }
      &__btn {
        position: absolute;
        left: 50%;
        bottom: -85px;
        transform: translate(-50%, 0);
      }
    }
  }
  .advantages {
    &__slider {
      margin-top: 60px;
    }
    &__btns {
      margin-bottom: 20px;
    }
  }
  .our-contacts {
    &__img-bg {
      display: none;
    }
    &__inner {
      display: block;
    }
    &__left {
      max-width: 100%;
      margin-right: 0;
    }
    &__right {
      width: 100%;
      height: 500px;
      margin-top: 30px;
    }
    &__title {
      display: flex;
      justify-content: center;
    }
  }
}

@media screen and (max-width: 1100px) {
  .services {
    .swiper-wrapper {
      grid-template-columns: repeat(4,calc((100% / 4) - (22px * 3 / 4)));
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    &__title {
      font-size: 45px;
    }
    &__text {
      font-size: 22px;
    }
    &__form {
      flex-direction: column;
      margin-top: 100px;
      &-group {
        &:not(:first-child) {
          margin: 50px 0 0 0;
        }
      }
      &-btn {
        margin: 50px 0 0 0;
      }
    }
    &__bottom {
      margin-top: 100px;
    }
  }
  .services {
    &__img {
      display: none;
    }
    .swiper-wrapper {
      grid-template-columns: repeat(3,calc((100% / 3) - (22px * 2 / 3)));
    }
  }
  .brands {
    padding: 65px 0 25px;
    .swiper {
      padding: 0 15px;
    }
    &__btns {
      margin-bottom: 20px;
    }
  }
  .prices {
    &__img {
      display: none;
    }
    &__img2 {
      display: none;
    }
    &__content {
      grid-template-columns: 1fr;
      gap: 15px;
    }
  }
  .works {
    &__img-bg {
      display: none;
    }
  }
  .cost {
    min-height: 700px;
    &__title {
      br {
        display: block;
      }
    }
  }
}

@media screen and (max-width: 768px) {
