@media screen and (max-width: 576px) {
  .main {
    &__item {
      &-icon {
       img {
         width: 43px;
         height: auto;
       }
      }
      &-desc {
        font-size: 14px;
      }
    }
  }
  .services {
    &__card {
      &-desc {
        font-size: 15px;
      }
    }
  }
  .brands {
    &__btn {
      button {
        width: calc(100% - 20px);
      }
    }
  }
  .prices {
    &__btn {
      button {
        width: calc(100% - 20px);
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .main {
    &__form-group {
      input {
        width: 100%;
      }
    }
  }
  .prices {
    &__item {
      display: block;
      min-height: 80px;
      padding: 10px 15px;
