.contacts {
  padding: 0 0 190px;
  position: relative;
  &__img {
    position: absolute;
    left: 50%;
    bottom: 30px;
    transform: translate(-50%, 0);
  }
  .display2 {
    &:after {
      left: 0;
      transform: translate(0);
    }
  }
  &__items {
    display: flex;
    align-items: center;
    margin: 80px auto 0;
    max-width: 1330px;
  }
  &__item {
    width: 250px;
    min-height: 225px;
    background: #FFFFFF;
    box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.1);
    padding: 40px 30px 20px;
    display: flex;
    flex-direction: column;
    text-align: center;
    i {
      width: 76px;
      height: 76px;
      margin:  0 auto;
      img {
        max-width: 100%;
        max-height: 100%;
      }
    }
    &:not(:first-child) {
      margin-left: 108px;
    }
  }
  &__numbers {
    margin-top: 20px;
  }
  &__subtitle {
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
  }
  &__number {
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
    display: block;
    margin-top: 7px;
  }
  &__location-wrap {
    margin-top: 20px;
    span {
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
      display: block;
      &:not(:first-child) {
        margin-top: 7px;
      }
    }
  }
  &__mail {
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
    margin-top: 7px;
    display: block;
  }
  &__mail-wrap {
    margin-top: 20px;
    span {
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
      display: block;
    }
  }
  &__chart-wrap {
    margin-top: 20px;
    span {
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
      display: block;
      &:not(:first-child) {
        margin-top: 7px;
      }
    }
  }
  .map2 {
    width: 100%;
    height: 445px;
    margin-top: 80px;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
    }
  }
}