.repair {
  padding: 70px 0 20px;
  position: relative;
  &__img-bg {
    position: absolute;
    top: 0;
    left: 0;
  }
  &__inner {
    position: relative;
  }
  &__nav {
    display: flex;
    justify-content: flex-end;
  }
  &__list {
    &-item {
      display: flex;
      align-items: center;
      position: relative;
      z-index: 5;
      &--active {
        span:nth-child(2) {
          text-decoration: underline;
        }
      }
      &:not(:first-child) {
        margin-top: 12px;
      }
      span {
        &:nth-child(1) {
          font-size: 14px;
          line-height: 120%;
          width: 22px;
          height: 22px;
          border-radius: 50%;
          border: 1px solid #F8970A;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-shrink: 0;
        }
        &:nth-child(2) {
          font-size: 18px;
          line-height: 120%;
          margin-left: 12px;
          display: block;
        }
      }
    }
  }
  &__images {
    max-width: 1010px;
    margin: -325px auto 0;
    position: relative;
  }
  &__item {
    position: absolute;
    top: 37%;
    left: 12%;
    &--active {
      .repair__item-icon:after {
        opacity: 1;
      }
      .repair__item-desc {
        opacity: 1;
      }
    }
    &2 {
      top: 42%;
      left: 20%;
    }
    &3 {
      top: 61%;
      left: 18%;
    }
    &4 {
      top: 50%;
      left: 4%;
    }
    &5 {
      top: 46%;
      left: 32%;
    }
    &6 {
      top: 52%;
      left: 36%;
    }
    &7 {
      top: 83%;
      left: 54%;
    }
    &8 {
      top: 73%;
      left: 55%;
    }
    &9 {
      top: 60%;
      left: 68%;
    }
    &10 {
      top: 71%;
      left: 77%;
    }
    &-content {
      position: relative;
    }
    &-icon {
      width: 60px;
      height: 60px;
      background: #FFFFFF;
      border: 1px solid #F8970A;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      z-index: 2;
      cursor: pointer;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: calc(100% + 8px);
        height: calc(100% + 8px);
        border: 1px solid #F8970A;
        border-radius: 50%;
        opacity: 0;
      }
    }
    &-desc {
      position: absolute;
      z-index: 3;
      right: 50px;
      bottom: 50px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &-info {
      display: flex;
      align-items: center;
      padding-bottom: 10px;
      span {
        &:nth-child(1) {
          font-size: 16px;
          line-height: 120%;
          width: 30px;
          height: 30px;
          border-radius: 50%;
          border: 1px solid #F8970A;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-shrink: 0;
          background: #FFFFFF;
        }
        &:nth-child(2) {
          font-size: 16px;
          line-height: 120%;
          margin-left: 12px;
          display: block;
          background: #FFFFFF;
          padding: 2px;
        }
      }
    }
  }
  &__img-car {
    img {
      width: 100%;
      height: auto;
    }
  }
}
.desc {
  padding: 40px 0 0;
 &__text {
