@media screen and (max-width: 1500px) {
  .footer {
    &__items {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 70px;
    }
    &__item {
      &:not(:first-child) {
        margin-left: 0;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    padding: 20px 0 60px;
    &__items {
      grid-template-columns: 1fr;
      gap: 15px;
    }
    &__logo {
      padding-bottom: 0;
    }
    &__service {
      font-size: 14px;
      margin-top: 15px;
    }
    &__list {
      li {
        font-size: 14px;
        &:not(:first-child) {
          margin-top: 15px;
        }
      }
    }
    &__numbers {
      margin-left: 15px;
    }
    &__number {
      font-size: 14px;
    }
    &__location {
      margin-top: 15px;
      span {
        font-size: 14px;
        margin-left: 15px;
      }
    }
    &__mail {
      margin-top: 15px;
      span {
        font-size: 14px;
        margin-left: 15px;
      }
    }
    &__chart {
      margin-top: 15px;
      span {
        font-size: 14px;
        margin-left: 15px;
      }
    }
  }
}